@mixin input-placeholder {
	&.placeholder { @content; }
	&:-moz-placeholder { @content; }
	&::-moz-placeholder { @content; }
	&:-ms-input-placeholder { @content; }
	&::-webkit-input-placeholder { @content; }
}

//   placeholder
@mixin hidePlaceholder($time: 300, $effect: ease) {
  &::-webkit-input-placeholder       {opacity: 1; transition: opacity #{$time} + ms $effect;}
  &::-moz-placeholder                {opacity: 1; transition: opacity #{$time} + ms $effect;}
  &:-moz-placeholder                 {opacity: 1; transition: opacity #{$time} + ms $effect;}
  &:-ms-input-placeholder            {opacity: 1; transition: opacity #{$time} + ms $effect;}
  &:focus::-webkit-input-placeholder {opacity: 0; transition: opacity #{$time} + ms $effect;}
  &:focus::-moz-placeholder          {opacity: 0; transition: opacity #{$time} + ms $effect;}
  &:focus:-moz-placeholder           {opacity: 0; transition: opacity #{$time} + ms $effect;}
  &:focus:-ms-input-placeholder      {opacity: 0; transition: opacity #{$time} + ms $effect;}
}

@mixin pseudo($display: block, $pos: absolute, $content: '') {
	content: $content;
	display: $display;
	position: $pos;
}

@mixin align($vertical: true, $horizontal: false, $position: relative) {
	@if $position {
		position: $position;
	}
	@if $vertical {
		top: 50%;
	}
	@if $horizontal {
		left: 50%;
	}

	@if $vertical and $horizontal {
		transform: translateX(-50%) translateY(-50%);
	} @else if $vertical {
		transform: translateY(-50%);
	} @else {
		transform: translateX(-50%);
	}
}

@mixin mq($resolution) {
  @media (max-width: $resolution) {
    @content;
  }
}

//     - ,    3  (...)
@mixin lineClamp($value) {
  display: -webkit-box;
  -webkit-line-clamp: $value;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

//    inline 
@mixin justify {
	text-align: justify;
	line-height: 0;
	font-size: 0;
	text-justify: newspaper;
	zoom: 1;
	text-align-last: justify;
	&:after {
		content: "";
		display: inline-block;
		width: 100%;
		height: 0px;
		visibility: hidden;
		overflow: hidden;
	}
}

//    inline 
@mixin vertical {
	text-align: center;
	font-size: 0;
	&:before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		width: 0;
		height: 100%;
	}
}

//  
// : @include sep-h(80%, 2px, blue);
@mixin sep-h($w, $h, $color) {
	&:after {
		content: "";
		display: block;
		height: $h;
		width: $w;
		margin: 0 auto;
		background-color: $color;
	}
}

// input style custom
// : @include input($green,$black,$black,.5s);
@mixin input($bsh: transparentize(blue,.3), $bdc: gray, $pl: gray, $timetransit: .15s){
	display: block;
	width: 100%;
	height: 34px;
	padding: 6px 12px;
	font-size: 14px;
	line-height: 1.42857143;
	color: #555;
	background-color: #fff;
	background-image: none;
	border: 1px solid $bdc;
	border-radius: 4px;
	-webkit-box-shadow: inset 0 0 1px $bdc;
					box-shadow: inset 0 0 1px $bdc;
	-webkit-transition: border-color ease-in-out $timetransit, -webkit-box-shadow ease-in-out $timetransit;
			 -o-transition: border-color ease-in-out $timetransit, box-shadow ease-in-out $timetransit;
					transition: border-color ease-in-out $timetransit, box-shadow ease-in-out $timetransit;
	&:focus {
		border-color: $bsh;
		outline: 0;
		-webkit-box-shadow: inset 0 0 1px $bdc, 0 0 8px $bsh;
						box-shadow: inset 0 0 1px $bdc, 0 0 8px $bsh;
	}
	&::-moz-placeholder{
		color: $pl;
		opacity: 1;
	}
	&:-ms-input-placeholder{
		color: $pl;
	}
	&::-webkit-input-placeholder{
		color: $pl;
	}
	&::-ms-expand{
		background-color: transparent;
		border: 0;
	}
}
