.footer {
  padding-top: 6px;
  padding-bottom: 3px;
  background-color: var(--color-MineShaft);
  @include mq(880px) {
    padding-top: 24px;
    padding-bottom: 22px;
  }
  @include mq(576px) {
    padding-top: 31px;
    padding-bottom: 0;
  }
}

.footer__container {
  display: flex;
  align-items: center;
  @include mq(880px) {
    justify-content: space-between;
  }
  @include mq(576px) {
    flex-direction: column-reverse;
    margin-right: -21px;
    margin-left: -21px;
  }
}

.footer__logo {
  margin-right: 203px;
  @include mq(1100px) {
    margin-right: 137px;
  }
  @include mq(980px) {
    margin-right: 50px;
  }
  @include mq(576px) {
    margin-right: 0;
    padding-top: 14px;
    padding-bottom: 14px;
    width: 100%;
    background-color: var(--color-Tundora);
  }
}

.footer__logo--img {
  display: block;
  margin-bottom: 6px;
  @include mq(1024px) {
    width: 166px;
  }
  @include mq(576px) {
    margin: 0 auto;
    margin-bottom: 3px;
    width: 112px;
  }
}

.footer__logo--text {
  margin: 0;
  @include font-firsNeue(400, 14px, 18px, var(--color-Mercury));
  @include mq(576px) {
    margin: 0 auto;
    width: 171px;
    font-size: 10px;
    line-height: 13px;
  }
}

.footer__text {
  margin-right: auto;
  width: 361px;
  @include font-roboto(400, 16px, 22px, var(--color-Silver));
  @include mq(880px) {
    display: none;
  }
}

.footer__copyright {
  display: block;
}

.footer__social {
  @include mq(576px) {
    margin-bottom: 32px;
  }
}

.footer__social--text {
  margin: 0;
  margin-bottom: 8px;
  @include font-roboto(400, 16px, 22px, var(--color-Silver));
}

.footer__social--list {
  display: flex;
  justify-content: center;
  li:not(:last-child) {
    margin-right: 20px;
  }
}

.social__link {
  svg {
    display: block;
    path {
      transition: fill .3s linear;
    }
  }
  &:focus{
    outline: none;
    svg path {
      fill: var(--color-Tuscany);
    }
	}
	&:hover {
    svg path {
      fill: var(--color-BurningOrange);
    }
	}
	&:active {
    svg path {
      fill: var(--color-Grenadier);
    }
	}
}
