
[class*="copyrights-pane"] {
	display: none !important;
}

[class*="ground-pane"] {
	filter: grayscale(1);
}

.contacts {
  padding-bottom: 62px;
  @include mq(768px) {
    padding-bottom: 69px;
  }
  @include mq(576px) {
    padding-bottom: 50px;
  }
}

.contacts__title {
  margin: 0;
  margin-bottom: 22px;
  @include font-firsNeue(700, 24px, 31px, var(--color-MineShaft));
  @include mq(576px) {
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 23px;
  }
}

.contacts__content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  @include mq(1024px) {
    grid-template-columns: repeat(8, 1fr);
  }
  @include mq(768px) {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, auto);
    row-gap: 24px;
  }
}

.contacts__map {
  position: relative;
  min-height: 460px;
  grid-column: 1 / 9;
  @include mq(1024px) {
    grid-column: 1 / 6;
  }
  @include mq(768px) {
    grid-column: 1 / 7;
  }
  @include mq(576px) {
    margin-left: -21px;
    margin-right: -21px;
    min-height: 472px;
  }
}

.map__address {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50%;
  max-width: 410px;
  height: 100%;
  padding: 175px 50px 10px 60px;
  background-color: var(--color-Abbey);
  transition: visibility 0.5s linear, opacity 0.5s linear;
  &.hidden {
    visibility: hidden;
    opacity: 0;
  }
  &.active {
    visibility: visible;
    opacity: 1;
  }
  z-index: 1;
  @include mq(1024px) {
    padding: 25px 260px 10px 30px;
    right: 0;
    max-height: 157px;
    width: 100%;
    max-width: 100%;
  }
  @include mq(985px) {
    padding-right: 30px;
  }
  @include mq(768px) {
    padding-right: 250px;
  }
  @include mq(576px) {
    padding: 26px 34px 10px 18px;
  }
}

.map__address--close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.map__title {
  margin: 0;
  margin-bottom: 5px;
  @include font-firsNeue(400, 24px, 22px, var(--color-White));
  @include mq(576px) {
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 22px;
  }
}

.map__address-text {
  @include font-roboto(400, 16px, 28px, var(--color-Silver));
  font-style: normal;
  @include mq(576px) {
    font-size: 14px;
    line-height: 18px;
  }
}

.map__phone {
  display: block;
  @include font-firsNeue(400, 16px, 21px, var(--color-BurningOrange));
  @include mq(1024px) {
    display: inline;
  }
  @include mq(985px) {
    display: block;
  }
  @include mq(576px) {
    margin-top: 11px;
    font-size: 14px;
    line-height: 18px;
  }
}

.contacts__submit {
  grid-column: 9 / 13;
  @include mq(1024px) {
    grid-column: 6 / 9;
  }
  @include mq(768px) {
    grid-column: 1 / 5;
    grid-row: 2;
  }
  @include mq(576px) {
    grid-column: 1 / 7;
  }
}

.contacts__submit-title {
  margin: 0;
  margin-bottom: 5px;
  @include font-firsNeue(400, 24px, 22px, var(--color-MineShaft));
  @include mq(576px) {
    margin-bottom: 7px;
    font-size: 18px;
  }
}

.contacts__submit-descr {
  margin: 0;
  margin-bottom: 14px;
  @include font-roboto(400, 16px, 22px, var(--color-DustyGray));
  @include mq(576px) {
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 18px;
  }
}

.contacts__form {
  display: grid;
  gap: 20px;
  justify-items: start;
}

.form__label {
  position: relative;
  width: 100%;
}

.form__input--contacts {
  display: block;
  width: 100%;
  padding: 15px 23px 16px;
  background-color: var(--color-Mercury);
  @include font-roboto(400, 16px, 19px, var(--color-MineShaft));
  @include input-placeholder {
    @include font-roboto(400, 16px, 19px, var(--color-DustyGray));
  }
  @include hidePlaceholder;
  &:focus {
    outline: none;
    border: 1px solid var(--color-BurningOrange);
  }
  &:hover {
    @include input-placeholder {
      color: var(--color-BurningOrange);
    }
  }
  @include mq(576px) {
    padding: 12px 23px;
    font-size: 14px;
    line-height: 16px;
    @include input-placeholder {
      font-size: 14px;
      line-height: 16px;
    }
  }
}

.form__input--area {
  resize: none;
  min-height: 150px;
}

.form__button--contacts {
  background-color: var(--color-BurningOrange);
  padding: 15px 22px;
  @include font-roboto(700, 16px, 19px, var(--color-White));
  @include btn-anim(1);
  @include mq(576px) {
    padding: 12px 22px;
    font-size: 14px;
    line-height: 16px;
  }
}

.just-validate-error-label {
  position: absolute;
  left: 23px;
  top: 3px;
  @include font-roboto(400, 9px, 11px, var(--color-RedOrange));
}

.just-validate-error-field {
  border: 1px solid var(--color-RedOrange);
}
