.projects {
  background-color: var(--color-White);
}

.projects__container {
  padding-top: 68px;
  padding-bottom: 68px;
  @include mq(1024px) {
  }
  @include mq(768px) {
  }
  @include mq(576px) {
    padding-top: 50px;
    padding-bottom: 34px;
  }
}

.projects__title {
  margin: 0;
  margin-bottom: 12px;
  @include font-firsNeue(700, 24px, 31px, var(--color-MineShaft));
  @include mq(576px) {
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 23px;
  }
}

.projects__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 30px;
  @include mq(1024px) {
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(3, auto);
    row-gap: 24px;
  }
  @include mq(768px) {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(4, auto);
    row-gap: 28px;
  }
  @include mq(576px) {
    grid-template-rows: repeat(7, auto);
    row-gap: 25px;
  }
}

.projects__item {

}

.item1 {
  grid-column: span 2;
  @include mq(1024px) {
  }
  @include mq(768px) {
  }
  @include mq(576px) {
    grid-column: span 6;
  }
}

.item2 {
  grid-column: span 2;
  @include mq(1024px) {
  }
  @include mq(768px) {
  }
  @include mq(576px) {
    grid-column: span 6;
  }
}

.item3 {
  grid-column: span 2;
  @include mq(1024px) {
    grid-column: span 4;
  }
  @include mq(768px) {
    grid-column: span 2;
  }
  @include mq(576px) {
    grid-column: span 6;
  }
}

.item4 {
  grid-column: span 6;
  @include mq(1024px) {
    grid-column: span 5;
  }
  @include mq(768px) {
    grid-column: span 6;
  }
  @include mq(576px) {
  }
}

.item5 {
  grid-column: span 6;
  @include mq(1024px) {
    grid-column: 4 / span 5;
    grid-row: 3;
  }
  @include mq(768px) {
    grid-column: span 6;
    grid-row: 4;
  }
  @include mq(576px) {
    grid-row: 7;
  }
}

.item6 {
  grid-column: span 3;
  @include mq(1024px) {
  }
  @include mq(768px) {
  }
  @include mq(576px) {
    grid-column: span 6;
  }
}

.item7 {
  grid-column: span 3;
  @include mq(1024px) {
  }
  @include mq(768px) {
  }
  @include mq(576px) {
    grid-column: span 6;
  }
}

.card {
  position: relative;
  height: 100%;
}

.card__img {
  position: relative;
  margin-bottom: 18px;
  @include mq(576px) {
    margin-bottom: 19px;
    img {
      width: 100%;
    }
  }
}

.card__img--big {
  margin-bottom: 0;
  height: 100%;
  img {
    height: 100%;
  }
}

.card__hashtag {
  position: absolute;
  top: 7px;
  right: 7px;
  padding: 6px 14px;
  background-color: var(--color-MineShaft);
  @include font-roboto(400, 14px, 16px, var(--color-White));
  @include btn-anim(1);
  z-index: 1000;
  @include mq(576px) {
    top: 5px;
    right: 5px;
    padding-top: 4px;
    padding-bottom: 4px;
    font-size: 12px;
    line-height: 14px;
  }
}

.card__text {
  margin: 0;
  margin-bottom: 10px;
  @include font-roboto(400, 16px, 22px, var(--color-MineShaft));
  @include lineClamp(3);
  @include mq(1024px) {
    margin-bottom: 17px;
  }
  @include mq(768px) {
    margin-bottom: 11px;
  }
  @include mq(576px) {
    margin-bottom: 8px;
  }
}

.card__text1 {
  @include mq(1024px) {
    @include lineClamp(2);
  }
  @include mq(768px) {
    @include lineClamp(3);
  }
}

.card__text2 {
  @include mq(576px) {
    @include lineClamp(2);
  }
}

.card__descr {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 22px 30px 30px;
  width: 322px;
  @include mq(1024px) {
    padding-top: 30px;
  }
  @include mq(677px) {
    width: 70%;
  }
  @include mq(576px) {
    width: 100%;
    justify-content: flex-end;
  }
}

.card__descr-top {
  margin-bottom: auto;
  @include mq(576px) {
    margin-bottom: 16px;
  }
}

.card__date {
  @include flex-v-center;
  @include font-roboto(400, 12px, 22px, var(--color-DustyGray));
}

.card__date--color {
  margin-bottom: 2px;
  color: var(--color-White);
  @include mq(576px) {
    margin-bottom: 7px;
  }
}

.card__icon {
  display: block;
  margin-right: 4px;
  width: 14px;
  height: 14px;
  @include bg('../img/date-icon/date-icon.png', $size: contain, $position: center);
}

.card__icon--white {
  background-image: url('../img/date-icon/date-icon-white.png');
}

.card__date-text {

}

.card__title {
  margin: 0;
  margin-bottom: 6px;
  @include font-firsNeue(700, 24px, 24px, var(--color-White));

}

.card__descr-text {
  margin: 0;
  @include font-roboto(400, 16px, 22px, var(--color-White));
  @include mq(576px) {
    font-size: 14px;
    line-height: 18px;
  }
}

.card__button{
  padding: 12.5px 19px;
  max-width: max-content;
  border: 3px solid var(--color-BurningOrange);
  @include font-roboto(700, 16px, 19px, var(--color-White));
  @include btn-anim();
  @include mq(576px) {
    padding: 9px 18px;
    font-size: 14px;
    line-height: 16px;
  }
}


