@mixin btn-anim($fill: false) {
	transition: all .2s linear;

  &:focus{
		outline: none;
	}

	&:focus-visible {
    @if $fill {
      background-color: var(--color-BurningOrange);
      outline: 3px solid var(--color-Grenadier);
      outline-offset: 0;
    } @else {
      background-color: var(--color-Tuscany);
    }
	}

	&:hover {
    @if $fill {
      background-color: var(--color-Tuscany);
      outline: none;
    } @else {
      background-color: var(--color-BurningOrange);
    }
	}

	&:active {
    @if $fill {
      background-color: var(--color-Grenadier);
    } @else {
      background-color: var(--color-Grenadier);
      border-color: var(--color-Grenadier);
    }
	}
}
