.aboutus {
  background-color: var(--color-MineShaft);
}

.aboutus__container {
  padding-top: 30px;
  padding-bottom: 66px;
  @include mq(1024px) {
    padding-bottom: 82px;
  }
  @include mq(768px) {
    padding-bottom: 61px;
  }
  @include mq(576px) {
    padding-top: 32px;
    padding-bottom: 58px;
  }
}

.aboutus__title {
  margin: 0;
  @include font-firsNeue(700, 24px, 31px, var(--color-White));
  margin-bottom: 5px;
  @include mq(576px) {
    font-size: 18px;
    line-height: 23px;
  }
}

.aboutus__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  @include mq(576px) {
    grid-template-columns: 1fr;
  }
}

.aboutus__text {
  margin: 0;
  @include font-roboto(400, 16px, 28px, var(--color-Silver));
  @include mq(576px) {
    margin-bottom: 22px;
    font-size: 14px;
    line-height: 25px;
  }
}

.subscribe {
  img {
    margin-bottom: 25px;
    @include mq(1024px) {
      margin-bottom: 21px;
    }
    @include mq(576px) {
      margin-bottom: 26px;
    }
  }
}

.subscribe__title {
  margin: 0;
  margin-bottom: 16px;
  @include font-firsNeue(400, 24px, 22px, var(--color-White));
  @include mq(576px) {
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 22px;
  }
}

.subscribe__form {
  display: flex;
  @include mq(1024px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.input-wrap {
  position: relative;
  margin-right: 25px;
  width: 100%;
  max-width: 474px;
  @include mq(1024px) {
    max-width: 342px;
    margin-right: 0;
    margin-bottom: 20px;
  }
  @include mq(576px) {
    margin-bottom: 22px;
  }
}

.subscribe__input {
  width: 100%;
  max-width: 474px;
  height: 50px;
  padding: 15px 23px;
  background-color: var(--color-Tundora);
  @include font-roboto(400, 16px, 19px, var(--color-Silver));

  @include input-placeholder {
    @include font-roboto(400, 16px, 19px, var(--color-Silver));
  }
  @include hidePlaceholder;
  &:focus {
    outline: none;
    border: 1px solid var(--color-BurningOrange);
  }
  &:hover {
    @include input-placeholder {
      color: var(--color-BurningOrange);
    }
  }
  &.input-error {
    border: 1px solid var(--color-BurntSienna);
  }
  @include mq(576px) {
    height: 40px;
  }
}

.error {
  display: none;
  position: absolute;
  left: 23px;
  top: 2px;
  @include font-roboto(400, 9px, 11px, var(--color-BurntSienna));
}

.subscribe__button {
  display: inline-block;
  background-color: var(--color-BurningOrange);
  padding: 15px 22px;
  @include font-roboto(700, 16px, 19px, var(--color-White));
  @include btn-anim(1);
  @include mq(576px) {
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 14px;
    line-height: 16px;
  }
}
