:root {
  --content-width: 1290px;
  --container-offset: 30px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --color-White: #fff;
  --color-Mercury: #e9e9e9;
  --color-Silver: #cacaca;
  --color-DustyGray: #999;
  --color-Tundora: #4a4a4a;
  --color-Abbey: rgba(32, 32, 32, 0.85);
  --color-MineShaft: #202020;

  --color-Romantic: #ffd4c1;
  --color-RedOrange: #ff3030;
  --color-BurntSienna: #f06666;
  --color-Grenadier: #ce4307;
  --color-Tuscany: #bc572b;
  --color-BurningOrange: #ff6e30;
}

@include mq(768px) {
  :root {
    --container-offset: 27px;
  }
}

@include mq(576px) {
  :root {
    --container-offset: 21px;
  }
}
