.hero {
  padding-top: 41px;
  padding-bottom: 68px;
  @include mq(1024px) {
    padding-top: 34px;
  }
  @include mq(576px) {
    padding-top: 30px;
    padding-bottom: 53px;
  }
}

.foto-works {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, auto);
  column-gap: 30px;
  row-gap: 24px;
  img {
    height: 100%;
  }
  @include mq(576px) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, auto);
    row-gap: 26px;
    img {
      width: 100%;
    }
  }
}

.foto-works__master {
  position: relative;
  grid-column: 1/3;
  grid-row: 1/4;
  @include mq(576px) {
    grid-column: 1/3;
    grid-row: 1;
  }
}

.master__descr {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 15px 15px 60px 60px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  @include mq(1024px) {
    padding: 15px 15px 30px 30px;
  }
  @include mq(576px) {
    padding: 5px 5px 30px 30px;
  }
  .hashtag {
    align-self: flex-end;
    background-color: var(--color-MineShaft);
    padding: 7px 15px;
    @include font-roboto(400, 14px, 16px, var(--color-White));
    @include btn-anim(1);
    @include mq(576px) {
      padding: 4px 7px;
      font-size: 12px;
      line-height: 14px;
    }
  }
  .master__text {
    margin: 0;
    margin-bottom: 3px;
    @include font-roboto(400, 16px, 19px, var(--color-White));
    @include mq(1024px) {
      margin-bottom: 9px;
    }
    @include mq(768px) {
      margin-bottom: 14px;
    }
    @include mq(576px) {
      margin-bottom: 10px;
      font-size: 14px;
      line-height: 16px;
    }
  }
  .master__title {
    margin: 0;
    @include font-firsNeue(700, 36px, 104%, var(--color-White));
    @include mq(1024px) {
      font-size: 34px;
      line-height: 100%;
    }
    @include mq(768px) {
      line-height: 90%;
    }
    @include mq(576px) {
      max-width: 169px;
      font-size: 24px;
      line-height: 110%;
    }
  }
  .master__time {
    margin: 0;
    margin-bottom: 19px;
    @include font-firsNeue(700, 36px, 104%, var(--color-White));
    @include mq(1024px) {
      margin-bottom: 22px;
      font-size: 34px;
      line-height: 100%;
    }
    @include mq(768px) {
      margin-bottom: 23px;
      line-height: 90%;
    }
    @include mq(576px) {
      margin-bottom: 14px;
      font-size: 24px;
      line-height: 110%;
    }
  }
  .master__link {
    display: inline-block;
    background-color: var(--color-BurningOrange);
    padding: 15px 22px;
    @include font-roboto(700, 16px, 19px, var(--color-White));
    @include btn-anim(1);
    @include mq(576px) {
      padding-top: 12px;
      padding-bottom: 12px;
      font-size: 14px;
      line-height: 16px;
    }
  }
}

.foto-works__title {
  grid-column: 3/5;
  grid-row: 1;
  margin: 0;
  @include font-firsNeue(700, 24px, 104%, var(--color-MineShaft));
  @include mq(768px) {
    font-size: 22px;
  }
  @include mq(576px) {
    grid-column: 1/3;
    grid-row: 2;
    font-size: 18px;
  }
}

.foto-works__foto1 {
  grid-column: 3/5;
  grid-row: 2;
  @include mq(576px) {
    grid-column: 1/3;
    grid-row: 3;
  }
}

.foto-works__foto2 {
  grid-column: 3;
  grid-row: 3;
  @include mq(576px) {
    grid-column: 1;
    grid-row: 4;
  }
}

.foto-works__foto3 {
  grid-column: 4;
  grid-row: 3;
  @include mq(576px) {
    grid-column: 2;
    grid-row: 4;
  }
}
