.header {
  background-color: var(--color-MineShaft);
}

.header__grid {
  position: relative;
  padding-top: 24px;
  padding-bottom: 25px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  align-items: center;

  @include mq(1024px) {
    grid-template-columns: repeat(8, 1fr);
    padding-top: 26px;
    padding-bottom: 27px;
  }

  @include mq(768px) {
    grid-template-columns: repeat(6, 1fr);
  }

  @include mq(576px) {
    grid-template-columns: repeat(2, 1fr);
    padding-top: 24px;
    padding-bottom: 26px;
  }
}

.header__logo {
  grid-column: 1/4;

  @include mq(1024px) {
    grid-column: 1/3;
    img {
      width: 166px;
    }
  }

  @include mq(576px) {
    img {
      width: 112px;
    }
    grid-column: 1;
  }

  img {
    display: block;
  }
}

.header__menu {
  grid-column: 4/12;

  @include mq(1024px) {
    grid-column: 3/8;
  }

  @include mq(768px) {
    display: none;
  }
}

.header__search-button {
  grid-column: 12/13;
  justify-self: end;

  @include mq(1024px) {
    grid-column: 8/9;
  }

  @include mq(768px) {
    grid-column: 6/7;
  }

  @include mq(576px) {
    svg {
      width: 20px;
      height: 20px;
    }

    grid-column: 2;
  }

  svg {
    display: block;
  }

  &:focus {
    outline: none;
    path {
      fill: var(--color-BurningOrange);
    }
  }

  &:hover {
    path {
      fill: var(--color-Tuscany);
    }
  }

  &:active {
    path {
      fill: var(--color-Grenadier);
    }
  }
}

.header__list {
  @include flex-v-center;

  li:not(:last-child) {
    margin-right: 79px;
  }

  @include mq(1185px) {
    li:not(:last-child) {
      margin-right: 33px;
    }
  }

  .header__link {
    @include font-firsNeue(400, 16px, 21px, var(--color-White));
    transition: color .3s linear, background-color .3s linear;
    &:focus {
      outline: none;
      background-color: var(--color-Tuscany);
    }

    &:hover {
      color: var(--color-BurningOrange);
      background-color: transparent;
    }

    &:active {
      color: var(--color-Grenadier);
      background-color: transparent;
    }
  }
}

.header__search-from {
  position: absolute;
  right: 0;
  top: 50%;

  @include flex-all-sb;

  width: 100%;
  max-width: 345px;
  // height: 30px;

  padding: 5px 2px 4px 1px;

  background-color: var(--color-MineShaft);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.12);

  z-index: 3000;

  visibility: hidden;
  transform: translateY(-250%);
  transition: visibility .5s linear, transform .5s linear;

  &.active {
    visibility: visible;
    transform: translateY(130%);
  }

  .search-from__button {
    svg {
      display: block;
    }

    &:focus {
      outline: none;
      circle {
        fill: var(--color-BurningOrange);
      }
    }

    &:hover {
      circle {
        fill: var(--color-Tuscany);
      }
    }

    &:active {
      circle {
        fill: var(--color-Grenadier);
      }
    }
  }

  .search-from__input {
    margin-right: 9px;
    width: 100%;
    border-bottom: 1px solid var(--color-Mercury);
    background-color: transparent;
    @include font-firsNeue(400, 16px, 21px, var(--color-Mercury));

    @include input-placeholder {
      @include font-firsNeue(400, 16px, 21px, var(--color-Mercury));
    }

    @include hidePlaceholder;

    &:focus {
      outline: none;
      border-bottom-color: var(--color-BurningOrange);
    }

    &:hover {
      border-bottom-color: var(--color-BurningOrange);
    }

    &:active {
      border-bottom-color: var(--color-Grenadier);
    }
  }
}

.header-bottom {
  display: none;
  background-color: var(--color-Tundora);
  padding-top: 10px;
  padding-bottom: 11px;

  .header__list {
    justify-content: space-between;

    li {
      margin-right: 0;
    }
  }

  .burger-button {
    display: none;
    svg {
      display: block;
    }
  }

  @include mq(768px) {
    display: block;
  }

  .menu-close,
  .phone {
    display: none;
  }

  @include mq(576px) {
    position: relative;
    padding-top: 14px;
    padding-bottom: 13px;

    .header-bottom__menu {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      min-width: 320px;
      height: 100vh;
      z-index: 2000;
      padding: 14px 18px;
      background-color: var(--color-MineShaft);
      overflow-y: auto;
      visibility: hidden;
      transform: translateX(-120%);
      transition: visibility .3s linear, transform .3s linear;
    }
    .header-bottom__menu--active {
      visibility: visible;
      transform: none;
    }
    .burger-button {
      display: block;
    }
    .menu-close {
      display: block;
      margin-bottom: 16px;
      &:focus {
        outline: none;
        circle {
          fill: var(--color-BurningOrange);
        }
      }
      &:hover {
        circle {
          fill: var(--color-Tuscany);
        }
      }
      &:active {
        circle {
          fill: var(--color-Grenadier);
        }
      }
    }

    .header__list {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 50px;

      li:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    .phone {
      display: block;
      max-width: max-content;
      @include font-firsNeue(500, 14px, 18px, var(--color-BurningOrange));
      transition: color .3s linear, background-color .3s linear;
      &:focus {
        outline-offset: 5px;
        outline: 2px solid var(--color-Tuscany);
      }

      &:hover {
        outline: none;
        color: var(--color-RedOrange);
        path {
          fill: var(--color-RedOrange);
        }
      }

      &:active {
        outline: none;
        color: var(--color-Grenadier);
        path {
          fill: var(--color-Grenadier);
        }
      }
    }
  }
}

.stop-scroll {
  overflow: hidden;
}
